<template>
  <div>
    <div class="beiJing">
      <div class="bJMask"></div>
    </div>
    <div class="ctn">
      <img @click="fanHui" src="@/assets/images/youJianTou.png" alt />
      <router-link tag="p" to="/reg">账号密码注册</router-link>
      <h2>欢迎您，请登录</h2>
      <div class="ipt">
        <input
          class="yongHu"
          type="text"
          placeholder="请输入用户名"
          v-model="uname"
        />
        <input
          class="miMa"
          type="password"
          placeholder="请输入密码"
          v-model="upwd"
        />
        <button @click="login">立即登录</button>
        <div class="yueDu">
          <input
            type="checkbox"
            v-model="yueDu"
            name="btn"
            id="btn1"
          />阅读并同意
          <a>《用户使用协议》</a>
          及
          <a>《隐私保护政策》</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import qs from "qs";
import { Toast } from "vant";
export default {
  data() {
    return {
      uname: "",
      upwd: "",
      yueDu: false
    };
  },
  methods: {
    fanHui() {
      this.$router.push("/my");
    },
    login() {
      var ureg = new RegExp(/^[a-z][a-zA-Z0-9_]{4,15}$/);
      var preg = new RegExp(/^[a-zA-Z]\w{5,17}$/);
      var u = this.uname;
      console.log(u);
      var p = this.upwd;
      console.log(p);
      if (!ureg.test(u)) {
        Toast("用户名格式为开头小写字母，4-15位");
        return;
      }
      if (!preg.test(p)) {
        Toast("密码格式为字母开头，5-17位");
        return;
      }
      //发送ajax请求
      var url = "user/login";
      this.axios
        .post(
          url,
          qs.stringify({
            uname: u,
            upwd: p
          })
        )
        .then(res => {
          console.log(res);
          if (res.data.code == 1) {
            if (this.yueDu == false) {
              Toast("请阅读并同意用户协议及隐私保护");
            } else {
              Toast.success("登陆成功");
              this.$router.push("/my");
              this.$store.commit("userLogin");
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
.ctn > p {
  position: absolute;
  top: 15px;
  right: -20px;
  color: #fff;
}
.ipt {
  display: flex;
  justify-content: space-around;
}
.ipt > input {
  font-size: 0.9rem;
  color: #fff;
}
.yueDu > a {
  color: #00dbc1;
}
.yueDu > div > div > input {
  margin-right: 4px;
  color: #00dbc1;
  border: none;
}
.yueDu {
  position: absolute;
  top: 400px;
  left: 50px;
  color: #fff;
  display: flex;
  justify-items: center;
  line-height: 10px;
  width: 100%;
  text-align: center;
}
.ctn > div > button {
  position: absolute;
  top: 340px;
  left: 51px;
  width: 75%;
  padding: 8px;
  border-radius: 20px;
  color: #fff;
  background-image: linear-gradient(90deg, #63e6dc, #00dbc1);
  border: none;
}
.ctn > h2 {
  font-weight: 600;
  font-size: 1.5rem;
  color: #fff;
  position: absolute;
  top: 70px;
  left: 25px;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: rgb(201, 201, 201) !important;
  font-size: 0.8rem;
}
.yongHu {
  position: absolute;
  background: transparent;
  width: 70%;
  top: 225px;
  border-bottom: 1px solid #fff;
  padding: 10px;
  left: 50px;
}
.miMa {
  position: absolute;
  width: 70%;
  border-bottom: 1px solid #fff;
  background: transparent;
  padding: 10px;
  top: 270px;
  left: 50px;
}
.ctn > img {
  position: absolute;
  left: 10px;
  top: 10px;
}
.beiJing > .bJMask {
  width: 100%;
  height: 100%;
  background: rgba(21, 21, 21, 0.4);
}
.beiJing {
  width: 100%;
  position: relative;
  height: 667px;
  background: url("../assets/images/d6cf8fbfc53d4dbf93dee23a227c530c.jpg")
    no-repeat;
  background-size: 100% 100%;
}
.ctn {
  width: 86%;
  position: absolute;
  left: 0;
  top: 0;
}
</style>



